"use client";

import { useState, useMemo } from "react";
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import { servicesData, categoryFilters } from "@/data/servicesData";
import "./services.css";

export const metadata = {
  title: "Services | The One Stop Studio",
};

export default function ServicesPage() {
  const [activeFilter, setActiveFilter] = useState<string>("all");

  // Build list with 'all' at front
  const filters = useMemo(() => ["all", ...categoryFilters], []);

  // Filter categories based on active filter
  const visibleCategories = useMemo(() => {
    if (activeFilter === "all") return servicesData;
    return servicesData.filter(cat => cat.category === activeFilter);
  }, [activeFilter]);

  return (
    <div className="min-h-screen bg-white">
      <Header />
      <main className="mx-auto max-w-6xl px-4 py-24">
        <h1 className="section-title">Our Services</h1>
        <div className="filter-buttons">
          {filters.map(f => (
            <button
              key={f}
              data-filter={f}
              className={activeFilter === f ? "active" : ""}
              onClick={() => setActiveFilter(f)}
            >
              {f === "all" ? "Show All" : f.split("/")[0].split(" ")[0]}
            </button>
          ))}
        </div>
        <div className="services-container" id="servicesContainer">
          {visibleCategories.map(category => (
            <div
              key={category.category}
              className="service-column"
              data-category={category.category}
            >
              <h3>{category.category}</h3>
              {category.services.map(service => (
                <div className="service-item" key={service.name}>
                  <h4>{service.name}</h4>
                  {service.price && service.price.trim() !== "" && (
                    <span className="price">{service.price}</span>
                  )}
                </div>
              ))}
            </div>
          ))}
        </div>
        <p style={{ textAlign: "center" }}>
          Full dynamic list. Call <a href="tel:+14247572546" className="underline">(424) 757-2546</a> for the latest.
        </p>
      </main>
      <Footer />
    </div>
  );
}
